@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url("https://fonts.googleapis.com/css?family=Open+Sans");
a.botui-message-content-link:focus {
  outline: thin dotted; }

a.botui-message-content-link:focus:active, a.botui-message-content-link:focus:hover {
  outline: 0; }

form.botui-actions-text {
  margin: 0; }

button.botui-actions-buttons-button, input.botui-actions-text-input {
  margin: 0;
  font-size: 100%;
  line-height: normal;
  vertical-align: baseline; }

button.botui-actions-buttons-button::-moz-focus-inner, input.botui-actions-text-input::-moz-focus-inner {
  border: 0;
  padding: 0; }

button.botui-actions-buttons-button {
  cursor: pointer;
  -webkit-appearance: button; }

.botui-app-container {
  width: 100%;
  height: 100%;
  line-height: 1; }

@media (min-width: 400px) {
  .botui-app-container {
    width: 400px;
    height: 500px;
    margin: 0 auto; } }

.botui-container {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden; }

.botui-message {
  margin: 10px 0;
  min-height: 20px; }

.botui-message:after {
  display: block;
  content: "";
  clear: both; }

.botui-message-content {
  width: auto;
  max-width: 75%;
  display: inline-block; }

.botui-message-content.human {
  float: right; }

.botui-message-content iframe {
  width: 100%; }

.botui-message-content-image {
  margin: 5px 0;
  display: block;
  max-width: 200px;
  max-height: 200px; }

.botui-message-content-link {
  text-decoration: underline; }

.profil {
  position: relative;
  border-radius: 50%; }

.profil.human {
  float: right;
  margin-left: 5px; }

.profil.agent {
  float: left;
  margin-right: 5px; }

.profil > img {
  width: 26px;
  height: 26px;
  border: 2px solid #e8e8e8; }

.profil > img.agent {
  content: url(http://decodemoji.com/img/logos/blue_moji_hat.svg);
  border-radius: 50%; }

button.botui-actions-buttons-button {
  margin-top: 10px;
  margin-bottom: 10px; }

button.botui-actions-buttons-button:not(:last-child) {
  margin-right: 10px; }

@media (min-width: 400px) {
  .botui-actions-text-submit {
    display: none; } }

.botui-container {
  font-size: 14px;
  background-color: #fff;
  font-family: "Open Sans",sans-serif; }

.botui-messages-container {
  padding: 10px 20px; }

.botui-actions-container {
  padding: 10px 20px; }

.botui-message {
  min-height: 30px; }

.botui-message-content {
  padding: 7px 13px;
  border-radius: 15px;
  color: #595a5a;
  background-color: #ebebeb; }

.botui-message-content.human {
  color: #f7f8f8;
  background-color: #919292; }

.botui-message-content.text {
  line-height: 1.3; }

.botui-message-content.loading {
  background-color: rgba(206, 206, 206, 0.5);
  line-height: 1.3;
  text-align: center; }

.botui-message-content.embed {
  padding: 5px;
  border-radius: 5px; }

.botui-message-content-link {
  color: #919292; }

.botui-actions-text-input {
  border: 0;
  outline: 0;
  border-radius: 0;
  padding: 5px 7px;
  font-family: "Open Sans",sans-serif;
  background-color: transparent;
  color: #595a5a;
  border-bottom: 1px solid #919292; }

.botui-actions-text-submit {
  color: #fff;
  width: 30px;
  padding: 5px;
  height: 30px;
  line-height: 1;
  border-radius: 50%;
  border: 1px solid #919292;
  background: #777979; }

.botui-actions-buttons-button {
  border: 0;
  color: #fff;
  line-height: 1;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  padding: 7px 15px;
  border-radius: 4px;
  font-family: "Open Sans",sans-serif;
  background: #777979;
  box-shadow: 2px 3px 4px 0 rgba(0, 0, 0, 0.25); }

.botui-actions-text-select {
  border: 0;
  outline: 0;
  border-radius: 0;
  padding: 5px 7px;
  font-family: "Open Sans",sans-serif;
  background-color: transparent;
  color: #595a5a;
  border-bottom: 1px solid #919292; }

.botui-actions-text-searchselect {
  border: 0;
  outline: 0;
  border-radius: 0;
  padding: 5px 7px;
  font-family: "Open Sans",sans-serif;
  background-color: transparent;
  color: #595a5a;
  border-bottom: 1px solid #919292; }

.botui-actions-text-searchselect .dropdown-toggle {
  border: none !important; }

.botui-actions-text-searchselect .selected-tag {
  background-color: transparent !important;
  border: 0 !important; }

.slide-fade-enter-active {
  transition: all .3s ease; }

.slide-fade-enter, .slide-fade-leave-to {
  opacity: 0;
  transform: translateX(-10px); }

.dot {
  width: .5rem;
  height: .5rem;
  border-radius: .5rem;
  display: inline-block;
  background-color: #919292; }

.dot:nth-last-child(1) {
  margin-left: .3rem;
  animation: loading .6s .3s linear infinite; }

.dot:nth-last-child(2) {
  margin-left: .3rem;
  animation: loading .6s .2s linear infinite; }

.dot:nth-last-child(3) {
  animation: loading .6s .1s linear infinite; }

@keyframes loading {
  0% {
    transform: translate(0, 0);
    background-color: #ababab; }
  25% {
    transform: translate(0, -3px); }
  50% {
    transform: translate(0, 0);
    background-color: #ababab; }
  75% {
    transform: translate(0, 3px); }
  100% {
    transform: translate(0, 0); } }

@font-face {
  font-weight: 400;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff"); }

@font-face {
  font-weight: 600;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensanssemibold.woff2") format("woff2"), url("../fonts/opensanssemibold.woff") format("woff"); }

@font-face {
  font-weight: 700;
  font-family: "Open Sans";
  font-style: normal;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  min-width: 320px;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  display: flex;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
  background-color: #ffffff;
  text-size-adjust: 100%;
  scroll-behavior: smooth; }

.container {
  width: 1400px;
  margin: 0 auto;
  max-width: 100%; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

.hide {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */ }

.main {
  flex-grow: 1;
  background: linear-gradient(to left, #F8F0FB 0%, #F8F0FB 50%, #211A1D 50%, #211A1D 100%); }

.main__container {
  display: flex;
  min-height: 100%; }

.main__chat {
  background-color: #211A1D;
  flex: 1 0 50%; }

.main__board {
  background-color: #F8F0FB;
  flex: 1 0 50%; }

.chat__inner {
  padding: 50px 30px; }

.board {
  position: relative; }

.board__inner {
  padding: 50px; }

.board__title {
  font-size: 70px;
  text-transform: uppercase;
  opacity: .1;
  font-weight: bold;
  line-height: 1.1;
  margin-top: 0; }

.board__car {
  position: absolute;
  bottom: 0;
  left: 0; }

.board__woman {
  position: absolute;
  bottom: 0;
  right: 0; }

.board__alkohol {
  position: absolute;
  bottom: 0;
  right: 208px; }

.board__text {
  color: #211A1D;
  font-style: italic;
  font-weight: 600; }

